<template>
  <div class="box-card">
    <LayoutBox one="拼团">
      <div class="tool_group">
        <div class="btn_group">
          <router-link to="/super/market/team_buy/add"><el-button type="success" size="small" icon="el-icon-plus" class="inquire" round>新增活动</el-button></router-link>
          <!-- <el-button type="danger" size="small" icon="el-icon-delete" round>批量删除</el-button> 后端暂时不写接口 -->
        </div>
        <el-form class="input_group" :model="form" :inline="true" size="mini">
          <el-form-item label="活动名称">
            <el-input v-model="form.activeName" placeholder="活动名称" />
          </el-form-item>
          <el-form-item label="时间筛选 ">
            <el-date-picker v-model="form.startTime" type="date" placeholder="开始日期"></el-date-picker>至
            <el-date-picker v-model="form.endTime" type="date" placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 80px" @click="onSearch">查询</el-button>
          </el-form-item>
        </el-form>
      </div>

      <el-table :data="teamBuyList" style="width: 100%">
        <el-table-column type="selection" width="55" align="center" />
        <el-table-column prop="activeName" label="名称" align="center" />
        <el-table-column label="活动时间" align="center" width="350">
          <template slot-scope="scope">
            <p>{{ scope.row.startTime }} 至 {{ scope.row.endTime }}</p>
          </template>
        </el-table-column>
        <el-table-column label="活动状态" align="center">
          <template slot-scope="scope">
            <span v-if="new Date().getTime() < scope.row.startTimeDate">未开始</span>
            <span v-else-if="new Date().getTime() > scope.row.startTimeDate && new Date().getTime() < scope.row.endTimeDate">进行中</span>
            <span v-else-if="new Date().getTime() > scope.row.endTimeDate">已结束</span>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label="创建时间" align="center" />
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.state === 0 ? '未启用' : '已启用' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button style="color: #5bd9be" type="text" @click="onUpdate(scope)">编辑</el-button>
            <el-button v-if="scope.row.state === 1" style="color: #fd9a30" type="text" @click="onEnd(scope)">终止</el-button>
            <el-button v-else-if="scope.row.state === 0" style="color: #fd9a30" type="text" @click="onStart(scope)">启用</el-button>
            <el-button style="color: #ff474c" type="text" @click="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="teamBuyTotal" @current-change="handleCurrentChange" />
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'BargainsList',

  components: {
    LayoutBox
  },

  data() {
    return {
      form: {}, // 查询
      teamBuyList: [], // 团购列表
      teamBuyTotal: 0, // 团购分页总条数
      activeType: 2, // 活动类型 0-普通 1-秒杀 2-团购
      pageNo: 1, // 分页的当前页
    }
  },

  created() {
    this.apiActivityList()
  },

  methods: {
    // 分页
    handleCurrentChange(pageNo) {
      this.pageNo = pageNo
      this.apiActivityList()
    },
    // 终止 0-上架 1-下架
    onEnd(scope) {
      this.apiActivityUpOrDown(scope.row.id, 0)
        .then((res) => {
          this.$message.success('已终止活动')
          scope.row.state = 0
        })
    },
    // 启用 0-上架 1-下架
    onStart(scope) {
      this.apiActivityUpOrDown(scope.row.id, 1)
        .then((res) => {
          this.$message.success('已启用活动')
          scope.row.state = 1
        })
    },
    // 删除
    onDelete(scope) {
      this.apiActivityDelete(scope)
    },
    // 编辑
    onUpdate(scope) {
      console.log(scope)
      this.$router.push({ path: '/mark/list/teamBuyList/addTeamBuy', query: { id: scope.row.id }})
    },
    // 查询
    onSearch () {
      this.apiActivityList()
    },

    // api接口 ----------------------------
    // 获取团购列表
    apiActivityList() {
      let data = {
        pageNo: this.pageNo,
        activeType: this.activeType,
        type: 1, // 商家的
      }
      Object.assign(data, this.form) // 模糊查询
      this.$http.post('/woho/activity/manage/query', data)
        .then((res) => {
          this.teamBuyList = res.data.data.list
          this.teamBuyTotal = res.data.data.total
          // 把时间转换成时间戳再在对象里面添加进去
          this.teamBuyList.map((item) => {
            item.startTimeDate = new Date(item.startTime.replace(/-/g, '/')).getTime()
            item.endTimeDate = new Date(item.endTime.replace(/-/g, '/')).getTime()
          })
        })
    },
    // 0-上架 1-下架
    apiActivityUpOrDown(id, state) {
      return this.$http.post(`/woho/activity/manage/upOrDown/${id}/${state}`)
        .then((res) => {
          if (res.data.success) {
            return
          } else {
            this.$message.success(res.data.msg)
          }
        })
    },
    // 删除活动
    apiActivityDelete(scope) {
      this.$http.delete(`/woho/activity/manage/delete/${scope.row.id} `)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('删除成功')
            this.teamBuyList.splice(scope.$index, 1)
          }
        })
    },
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    .tool_group{
      display: flex;
    }
    .input_group{
      margin-left: 30px;
    }
  }
</style>
